import PressQRCodeWeb from '../common/vue3/q-r-code';




import { getActClass } from './utils';


export default {
  name: 'PressActQRCodeDialog',
  options: {
    styleIsolation: 'shared',
  },
  components: {

    PressQRCodeWeb,





  },
  props: {
    show: {
      type: Boolean,
      default: false,
      required: false,
    },
    url: {
      type: String,
      default: null,
      required: false,
    },
    image: {
      type: String,
      default:
        'https://image-1251917893.file.myqcloud.com/2020/TIP-igame-H5/gh_9e4c56699893_258.jpg',
      required: false,
    },
    title: {
      type: String,
      default: '扫一扫二维码',
      required: false,
    },
    scanTitle: {
      type: String,
      default: '截图保存使用微信-扫一扫',
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
    hideTipStyle: {
      type: Boolean,
      default: false,
    },
  },
  emits: [
    'update:show',
  ],
  data() {
    return {};
  },
  methods: {
    closeDialog() {
      this.$emit('update:show', false);
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};
